/**
 * src/utils/ImageResizer.ts
 * 이미지 리사이징 및 압축을 위한 유틸리티 함수
 */

// 기본 최대 너비/높이 설정 (이 값보다 큰 이미지만 리사이징 됩니다)
const MAX_DIMENSION = 1200; // 1200px
// 기본 JPEG 품질 (0.0 ~ 1.0)
const DEFAULT_QUALITY = 0.8;

interface ResizeOptions {
  maxWidth: number;
  maxHeight: number;
  quality: number; // JPEG/WebP 품질 (0.0 to 1.0)
}

/**
 * 이미지 파일의 크기를 조정하고 압축하여 Blob 객체로 반환합니다.
 * @param file 원본 이미지 File 객체
 * @param options 리사이징 옵션 (최대 크기 및 품질)
 * @returns 리사이즈된 Blob 객체 (JPEG 포맷)
 */
export const resizeImage = (file: File, options: ResizeOptions): Promise<Blob> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.onload = (event) => {
      if (!event.target?.result) {
        return reject(new Error("파일 로드 실패"));
      }

      const img = new Image();
      img.onload = () => {
        let width = img.width;
        let height = img.height;

        // 리사이징이 필요한지 확인
        let ratio = 1;

        if (width > options.maxWidth) {
          ratio = options.maxWidth / width;
        }

        if (height * ratio > options.maxHeight) {
          ratio = options.maxHeight / height;
        }

        // 리사이징이 필요 없는 경우 (ratio가 1)에도, 품질 적용을 위해 캔버스 사용
        if (ratio !== 1) {
          width *= ratio;
          height *= ratio;
        }

        // 캔버스 생성 및 이미지 그리기
        const canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext('2d');

        if (ctx) {
          // 리사이징/압축을 위해 캔버스에 이미지 그리기 (여기서 리사이징 비율 적용)
          ctx.drawImage(img, 0, 0, width, height);

          // Blob 생성 및 품질 적용 (JPEG로 변환하여 압축)
          canvas.toBlob(
              (blob) => {
                if (blob) {
                  // 성공적으로 Blob 생성 후 resolve
                  resolve(blob);
                } else {
                  reject(new Error("Blob 생성 실패"));
                }
              },
              'image/jpeg', // 항상 JPEG로 변환하여 압축
              options.quality
          );
        } else {
          reject(new Error("Canvas context 획득 실패"));
        }
      };
      img.onerror = () => reject(new Error("이미지 로드 오류"));
      img.src = event.target.result as string;
    };

    reader.onerror = (error) => reject(error);
    reader.readAsDataURL(file);
  });
};

/**
 * Blob 객체를 File 객체로 변환합니다.
 * @param blob Blob 객체
 * @param fileName 원본 파일 이름
 * @param originalMimeType 원본 MIME 타입
 * @returns File 객체
 */
export const blobToFile = (blob: Blob, fileName: string, originalMimeType: string): File => {
  // 리사이징 후에는 확장자를 jpeg로 통일하거나 원본을 따르지만, 여기서는 파일 이름을 유지
  // toBlob으로 생성된 Blob의 type은 'image/jpeg'로 설정됩니다.
  // 파일 이름을 덮어쓰지 않고 새로운 File 객체를 반환
  return new File([blob], fileName, {
    type: blob.type || originalMimeType,
    lastModified: Date.now(),
  });
};

// 기본 옵션 (WritePage.tsx에서 사용할 것)
export const DEFAULT_RESIZE_OPTIONS: ResizeOptions = {
  maxWidth: MAX_DIMENSION,
  maxHeight: MAX_DIMENSION,
  quality: DEFAULT_QUALITY,
};